/**
 * dwDateTime directive - wrapper for ffLabel + ffDate + ffTime date/time selection
 */
export default angular.module('deskworks.dwDateTime', ['flexForms'])

  .directive('dwDateTime', function() {
    return {
      restrict: 'E',
      scope: true,

      compile: function(tElement, tAttrs) {
        const ngDisabled = tAttrs.ngDisabled ? ' ng-disabled="' + tAttrs.ngDisabled + '"' : '';
        const ffDisabled = tAttrs.ngDisabled ? ' ff-disabled="' + tAttrs.ngDisabled + '"' : '';
        const ffRequired = tAttrs.ffRequired ? ' ff-required="' + tAttrs.ffRequired + '"' : '';
        const ffDateOrder = tAttrs.ffDateOrder ? ' ff-date-order="' + tAttrs.ffDateOrder + '"' : '';
        const ffDateDiff = tAttrs.ffDateDiff ? ' ff-date-diff="' + tAttrs.ffDateDiff + '"' : '';
        const ffValidateAlert = tAttrs.ffValidateAlert ? ' ff-validate-alert="' + tAttrs.ffValidateAlert + '"' : '';
        const labelClass = tAttrs.labelClass ? ' class="' + tAttrs.labelClass + '"' : '';

        const tpl =
          '<ff-row>' +

            '<label flex="1 1 auto">' +
              '<ff-label flex="' + tAttrs.labelFlex + '"' + ffRequired + ngDisabled + labelClass + '>' + tAttrs.ffLabel + '</ff-label>' +
              '<ff-row flex="1 1 1%">' +
                '<ff-date name="{{\'' + (tAttrs.name || tAttrs.ffModel) + 'date\'}}" flex="1 2 150px" ff-model="inner.date"' + ffRequired + ffDisabled + ffDateOrder + ffDateDiff + ffValidateAlert + '></ff-date>' +
                '<ff-time name="{{\'' + (tAttrs.name || tAttrs.ffModel) + 'time\'}}" flex="' + '1 3 150px' + '" ff-model="inner.time"' + ffRequired + ffDisabled + ffDateOrder + ffDateDiff + ffValidateAlert + '></ff-time>' +
              '</ff-row>' +
             '</label>' +
          '</ff-row>';

        tElement.append(tpl);

        return this.link;
      },

      link: function($scope, $element, $attrs) {
        $scope.inner = {};

        const initDate = $scope.$eval($attrs.ffModel);
        if (initDate instanceof moment) innerFromModel(initDate); // init inner date and time with initial date
        $scope.$watch($attrs.ffModel, innerFromModel); // update inner date and time with ffModel

        /**
         * Watch for inner date and update ffModel
         */
        $scope.$watch('inner.date', (date, oldDate) => {
          const model = $scope.$eval($attrs.ffModel);

          if (!moment.isMoment(date) || !moment.isMoment(model)) return;
          if (moment.isMoment(oldDate) && date.isSame(oldDate)) return;

          $scope.newDate = date.clone().hour(model.hour()).minute(model.minute());
          $scope.$eval($attrs.ffModel + ' = newDate');
        });

        /**
         * Watch for inner time and update ffModel
         */
        $scope.$watch('inner.time', (time, oldTime) => {
          const model = $scope.$eval($attrs.ffModel);

          if (!moment.isMoment(time) || !moment.isMoment(model)) return;
          if (moment.isMoment(oldTime) && time.isSame(oldTime)) return;

          $scope.newDate = model.clone().hour(time.hour()).minute(time.minute());
          $scope.$eval($attrs.ffModel + ' = newDate');
        });

        /**
         * Updates inner date and time only if ffModel is really changed
         */
        function innerFromModel(model, oldModel) {
          if (!moment.isMoment(model) || moment.isMoment(oldModel) && model.isSame(oldModel)) return;
          $scope.inner.date = model.clone().second(0).millisecond(0);
          $scope.inner.time = model.clone().second(0).millisecond(0);
        }
      }
    };
  });
