import ReservationUnit from 'shared/services/inventory/reservation-unit.service';
import dwDateTimeDirective from 'shared/dw-controls/dw-date-time/dw-date-time.directive';
import niceTimeDirective from 'shared/nice-date-time/nice-time.directive';

export default angular.module('deskworks.userReservations', [
  'session',
  'deskworks.reservationService',
  ReservationUnit.name,
  dwDateTimeDirective.name,
  niceTimeDirective.name,
])

  .controller('UserReservationsCtrl', function($scope, $filter, $route, $translate, $timeout, dwAlerts,
                                               reservationService, ReservationUnit, reservationUnits,
                                               user, state, session, helper, settings, reservationFields) {
    const $ctrl = this;

    state.setPageTitle($translate.instant('USER_RESERVATIONS.PAGE_TITLE', { userName: user.fullNameCompany }));

    $ctrl.optsFilter = [
      { id: 'all', name: $translate.instant('USER_RESERVATIONS.FILTER.ALL') },
      { id: 'upcoming', name: $translate.instant('USER_RESERVATIONS.FILTER.UPCOMING') },
      { id: 'past', name: $translate.instant('USER_RESERVATIONS.FILTER.PAST') }
    ];

    $ctrl.filter = $ctrl.optsFilter[0].id;
    $ctrl.settings = settings;
    $ctrl.reservations = [];
    $ctrl.units = reservationUnits;
    $ctrl.params = {
      centerId: state.getCurrentCenterId(),
      userId: state.getCurrentUserId(),
      offset: 0,
      limit: 50,
    };

    /**
     * Fetch reservations from server
     */
    $ctrl.fetch = function() {
      if ($ctrl.eof || $ctrl.fetching) return $ctrl.fetching;
      $ctrl.fetching = reservationService.query($ctrl.params)
        .catch(err => dwAlerts.error(err, 'Failed to query reservations.'))
        .finally(() => { $ctrl.fetching = null; })
        .then(reservations => postProcessReservations(reservations))
        .then(reservations => {
          $ctrl.reservations = $ctrl.reservations.concat(reservations);
          $ctrl.params.offset = $ctrl.reservations.length;
          $ctrl.eof = reservations.length < $ctrl.params.limit;
        });
    };

    /**
     * On filter change handler
     */
    $ctrl.onFilter = function() {
      $ctrl.reservations = [];
      $ctrl.params.offset = 0;
      $ctrl.eof = false;
      $ctrl.params.from = $ctrl.filter === 'upcoming' ? moment() : null;
      $ctrl.params.to = $ctrl.filter === 'past' ? moment() : null;

      $ctrl.fetch();
    };

    /**
     * Watch for $ctrl.editingReservation.from and if date is changed (but not time),
     * synchronize date of $ctrl.editingReservation.to with date of $ctrl.editingReservation.from
     */
    $scope.$watch('$ctrl.editingReservation.from', function(newVal, oldVal) {
      if (!moment.isMoment(newVal) || !moment.isMoment(oldVal)) return;
      if (newVal.date() !== oldVal.date() || newVal.month() !== oldVal.month() || newVal.year() !== oldVal.year()) {
        $ctrl.editingReservation.to = moment([
          newVal.year(), newVal.month(), newVal.date(),
          $ctrl.editingReservation.to.hour() || newVal.hour(),
          $ctrl.editingReservation.to.minute() || newVal.minute(),
          0, 0
        ]);
      }
    });

    /**
     * Save Reservation handler
     */
    $ctrl.saveReservation = function() {
      if (!$ctrl.editingReservation || helper.pointFirstInvalid('.reservations-edit')) return;

      const data = {
        id: $ctrl.editingReservation.id,
        reservationUnitId: $ctrl.editingReservation.reservationUnitId,
        from: $ctrl.editingReservation.from,
        to: $ctrl.editingReservation.to,
        reservationNote: $ctrl.editingReservation.reservationNote
      };

      return reservationService.save(state.getCurrentCenterId(), state.getCurrentUserId(), data)
        .catch(err => dwAlerts.error(err, 'Failed to update reservation.'))
        .then(function() {
          delete $ctrl.editingReservation;
          $ctrl.onFilter();
        });
    };

    /**
     * Is reservation editable
     */
    $ctrl.isEditable = function(reservation) {
      return session.isAdmin() || session.isRole('member');
    };

    /**
     * Edit Reservation handler
     */
    $ctrl.editReservation = function(reservation) {
      if (session.isRole('member')) { // General Edit Reservation for members
        state.setParam('date', $filter('date')(reservation.from, 'yyyy-MM-ddTHH:mm:00Z'));
        return state.setPath('/edit-reservation/' + reservation.id);
      }
      if (session.isAdmin()) { // Special Edit Reservation for admins
        $ctrl.editingReservation = angular.copy(reservation);
      }
    };

    /**
     * Back button handler
     */
    $ctrl.back = function() {
      delete $ctrl.editingReservation;
    };

    /**
     * Cancel reservation handler
     */
    $ctrl.cancelReservation = function(reservation) {
      if (!confirm($translate.instant('USER_RESERVATIONS.CONFIRM_DELETE'))) return;

      return reservationService.delete(state.getCurrentCenterId(), state.getCurrentUserId(), reservation.id)
        .catch(err => dwAlerts.error(err, 'Failed to cancel reservation.'))
        .then(() => {
          const index = helper.findIndex($ctrl.reservations, reservation.id);
          $ctrl.reservations.splice(index, 1);
      });
    };

    /**
     * New reservation handler
     */
    $ctrl.newReservation = function() {
      return state.setPath('/new-reservation');
    };

    function postProcessReservations(reservations) {
      reservations.forEach(reservation => {
        const unit = reservationUnits.find(u => u.id === reservation.reservationUnitId) || {};

        reservation.resCategoryName = unit.reservationTypeName;

        reservation.fields = reservation.fields || {};
        reservation.$$fields = reservationFields
          .filter(f => f.reservationCategoryId === unit.reservationTypeId || f.reservationCategoryId === 0)
          .filter(f => reservation.fields[f.id] || reservation.fields[f.id] === 0 || reservation.fields[f.id] === false)
          .map(f => ({ name: f.name, value: f.$$filter(f, reservation.fields[f.id]) }));
      });
      return reservations;
    }
  });
